import { Component, Prop, Event, EventEmitter, h, Host } from '@stencil/core';

export type CardHeaderFeaturedIconStyle = 'outlined' | 'filled';
export type CardHeaderFeaturedIconColor = 'brand' | 'gray' | 'error' | 'warning' | 'success' | 'highlight';

@Component({
  tag: 'bh-card-header',
  styleUrl: 'bh-card-header.css',
  shadow: true,
})
export class BhCardHeader {
  /**
   * Whether to show the featured icon
   */
  @Prop() showFeaturedIcon: boolean = true;

  /**
   * The Material Symbol icon name for the featured icon
   */
  @Prop() featuredIcon: string = 'star';

  /**
   * The style of the featured icon
   */
  @Prop() featuredIconStyle: CardHeaderFeaturedIconStyle = 'outlined';

  /**
   * The color of the featured icon
   */
  @Prop() featuredIconColor: CardHeaderFeaturedIconColor = 'gray';

  /**
   * Whether to show the title
   */
  @Prop() showTitle: boolean = true;

  /**
   * The title text
   */
  @Prop() headerTitle: string = 'Card title';

  /**
   * Whether to show the badge
   */
  @Prop() showBadge: boolean = false;

  /**
   * The badge text
   */
  @Prop() badgeText: string = '';

  /**
   * Whether to show the supporting text
   */
  @Prop() showSupportingText: boolean = true;

  /**
   * The supporting text (description)
   */
  @Prop() supportingText: string = 'Card description';

  /**
   * Whether to show the dropdown menu button
   */
  @Prop() showDropdown: boolean = true;

  /**
   * Whether to show the divider
   */
  @Prop() showDivider: boolean = true;

  /**
   * Emitted when the dropdown button is clicked
   */
  @Event() bhDropdownClick!: EventEmitter<void>;

  private handleDropdownClick = () => {
    this.bhDropdownClick.emit();
  };

  render() {
    return (
      <Host>
        <div class="card-header">
          <div class="card-header-content">
            <div class="card-header-text-container">
              <div class="card-header-title-row">
                {this.showFeaturedIcon && (
                  <bh-featured-icon
                    size="sm"
                    icon={this.featuredIcon}
                    iconStyle={this.featuredIconStyle}
                    color={this.featuredIconColor}
                  />
                )}
                {this.showTitle && (
                  <span class="card-header-title">{this.headerTitle}</span>
                )}
                {this.showBadge && this.badgeText && (
                  <bh-badge
                    variant="blue"
                    size="sm"
                    emphasis="medium"
                    label={this.badgeText}
                  />
                )}
              </div>
              {this.showSupportingText && (
                <div class="card-header-supporting-row">
                  {this.showFeaturedIcon && <div class="card-header-spacer" />}
                  <span class="card-header-supporting-text">{this.supportingText}</span>
                </div>
              )}
            </div>
            {this.showDropdown && (
              <bh-button-icon
                hierarchy="quaternary"
                size="sm"
                iconName="more_vert"
                ariaLabel="More options"
                onBhClick={this.handleDropdownClick}
              />
            )}
          </div>
          <slot name="tabs" />
          {this.showDivider && <div class="card-header-divider" />}
        </div>
      </Host>
    );
  }
}
